<h1 id="用户标签" tabindex="-1"><a class="header-anchor" href="#用户标签" aria-hidden="true">#</a> 用户标签</h1>
<h2 id="获取所有标签" tabindex="-1"><a class="header-anchor" href="#获取所有标签" aria-hidden="true">#</a> 获取所有标签</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">user_tag</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$tags</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">user_tag</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// {</span>
